
import React, { useCallback, useState } from "react";
import { useEditor, EditorContent } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";
import Link from "@tiptap/extension-link";
import Highlight from '@tiptap/extension-highlight';
import Youtube from '@tiptap/extension-youtube';
import TextAlign from "@tiptap/extension-text-align";
import { TextStyle } from '@tiptap/extension-text-style';
import FontSize from '@tiptap/extension-font-size';
import Placeholder from '@tiptap/extension-placeholder';
import Image from "@tiptap/extension-image";


import CustomSelect from './ui/CustomSelect';
import CustomPopover from './ui/CustomPopover';
import BoldIcon from '../icons/BoldIcon';
import UnderlineIcon from '../icons/UnderlineIcon';
import Highlighter from '../icons/Highlighter';
import CodeIcon from '../icons/CodeIcon';
import BulletListIcon from '../icons/BulletListIcon';
import OrderedListIcon from '../icons/OrderedListIcon';
import LinkIcon from '../icons/LinkIcon';
import ImageIcon from '../icons/ImageIcon';
import VideoIcon from '../icons/VideoIcon';
import AlignLeft from '../icons/AlignLeft';
import AlignCenter from '../icons/AlignCenter';
import AlignRight from '../icons/AlignRight';
import AlignJustify from '../icons/AlignJustify';
import FontSizeIcon from '../icons/FontSizeIcon';

// Import CSS inside the component so it is bundled
import './RicherEditor.css';

const fontSizes = [
  { name: '10px', value: '10px' },
  { name: '12px', value: '12px' },
  { name: '14px', value: '14px' },
  { name: '16px', value: '16px' },
  { name: '18px', value: '18px' },
  { name: '20px', value: '20px' },
  { name: '22px', value: '22px' },
  { name: '24px', value: '24px' },
  { name: '26px', value: '26px' },
  { name: '28px', value: '28px' },
  { name: '30px', value: '30px' },
  { name: '32px', value: '32px' },
];

const fontFamilies = [
  { name: 'Sans-serif', value: 'sans-serif' },
  { name: 'Serif', value: 'serif' },
  { name: 'Monospace', value: 'monospace' },
  { name: 'Arial', value: 'Arial, sans-serif' },
  { name: 'Georgia', value: 'Georgia, serif' },
  { name: 'Inter', value: 'Inter, sans-serif' },
];

const alignmentOptions = [
  { value: 'left', label: <AlignLeft size={18} />, name: 'Left' },
  { value: 'center', label: <AlignCenter size={18} />, name: 'Center' },
  { value: 'right', label: <AlignRight size={18} />, name: 'Right' },
  { value: 'justify', label: <AlignJustify size={18} />, name: 'Justify' },
];

// Add defaultI18n for button labels
const defaultI18n = {
  bold: 'Bold',
  underline: 'Underline',
  highlight: 'Highlight',
  code: 'Code Block',
  bulletList: 'Bullet List',
  orderedList: 'Ordered List',
  link: 'Insert/Edit Link',
  image: 'Insert Image',
  video: 'Insert YouTube Video',
  align: 'Align',
  insert: 'Insert',
  remove: 'Remove',
  add: 'Add',
  cancel: 'Cancel',
  fontSize: 'Font Size',
};

// Update MenuBar to accept excludeToolbarButtons and i18n
const MenuBar = ({ editor, imageUploadUrl, excludeToolbarButtons = [], i18n = {}, fontSizeOptions, fontFamilyOptions }: { editor: any, imageUploadUrl?: string, excludeToolbarButtons?: string[], i18n?: Record<string, string>, fontSizeOptions?: { name: string; value: string }[], fontFamilyOptions?: { name: string; value: string }[] }) => {
  const [imagePopoverOpen, setImagePopoverOpen] = useState(false);
  const [imageTab, setImageTab] = useState<'url' | 'upload'>('url');
  const [imageUrl, setImageUrl] = useState('');
  const [imageWidth, setImageWidth] = useState('');
  const [imageHeight, setImageHeight] = useState('');
  const [uploading, setUploading] = useState(false);
  const [uploadError, setUploadError] = useState<string | null>(null);
  const [uploadedImageUrl, setUploadedImageUrl] = useState('');
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [videoPopoverOpen, setVideoPopoverOpen] = useState(false);
  const [videoUrl, setVideoUrl] = useState('');
  const [videoWidth, setVideoWidth] = useState('');
  const [videoHeight, setVideoHeight] = useState('');
  const [linkPopoverOpen, setLinkPopoverOpen] = useState(false);
  const [linkUrl, setLinkUrl] = useState('');
  const [linkTarget, setLinkTarget] = useState('_blank');
  const linkButtonRef = React.useRef<HTMLButtonElement>(null);
  const imageButtonRef = React.useRef<HTMLButtonElement>(null);
  const videoButtonRef = React.useRef<HTMLButtonElement>(null);

  const labels = { ...defaultI18n, ...i18n };

  // Helper for image by URL
  const handleImageUrlInsert = useCallback(() => {
    if (imageUrl) {
      const attrs: any = { src: imageUrl };
      if (imageWidth) attrs.width = imageWidth;
      if (imageHeight) attrs.height = imageHeight;
      editor.chain().focus().setImage(attrs).run();
      setImagePopoverOpen(false);
      setImageUrl('');
      setImageWidth('');
      setImageHeight('');
    }
  }, [editor, imageUrl, imageWidth, imageHeight]);

  // Helper for image upload
  const handleImageUpload = async (file: File) => {
    if (!imageUploadUrl) return;
    setUploading(true);
    setUploadError(null);
    setUploadedImageUrl('');
    try {
      const formData = new FormData();
      formData.append('file', file);
      // If tiptap expects a specific field name, adjust here
      const response = await fetch(imageUploadUrl, {
        method: 'POST',
        body: formData,
      });
      if (!response.ok) throw new Error('Upload failed');
      const data = await response.json();
      // Tiptap expects { link: 'url' } or similar; adjust as needed
      const url = data.link || data.url || data.src || '';
      if (!url) throw new Error('No image URL returned');
      setUploadedImageUrl(url);
    } catch (err: any) {
      setUploadError(err.message || 'Upload failed');
    } finally {
      setUploading(false);
    }
  };

  const handleUploadInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setImageFile(file);
      handleImageUpload(file);
    }
  };

  const handleUploadedImageInsert = () => {
    if (uploadedImageUrl) {
      const attrs: any = { src: uploadedImageUrl };
      if (imageWidth) attrs.width = imageWidth;
      if (imageHeight) attrs.height = imageHeight;
      editor.chain().focus().setImage(attrs).run();
      setImagePopoverOpen(false);
      setUploadedImageUrl('');
      setImageFile(null);
      setImageWidth('');
      setImageHeight('');
      setUploadError(null);
      setImageTab('url');
    }
  };

  // Helper for video embedding (popover)
  const handleVideoUrlInsert = useCallback(() => {
    if (videoUrl) {
      const attrs: any = { src: videoUrl };
      if (videoWidth) attrs.width = videoWidth;
      if (videoHeight) attrs.height = videoHeight;
      editor.chain().focus().setYoutubeVideo(attrs).run();
      setVideoPopoverOpen(false);
      setVideoUrl('');
      setVideoWidth('');
      setVideoHeight('');
    }
  }, [editor, videoUrl, videoWidth, videoHeight]);

  // Helper for link insertion (popover)
  const handleLinkInsert = useCallback(() => {
    if (linkUrl) {
      editor.chain().focus().setLink({ href: linkUrl, target: linkTarget }).run();
      setLinkPopoverOpen(false);
      setLinkUrl('');
      setLinkTarget('_blank');
    }
  }, [editor, linkUrl, linkTarget]);
  const handleLinkUnset = useCallback(() => {
    editor.chain().focus().unsetLink().run();
    setLinkPopoverOpen(false);
    setLinkUrl('');
  }, [editor]);

  if (!editor) return null;

  return (
    <>
      <div className="richer-editor-toolbar">
        {/* Font Size Dropdown */}
        {!excludeToolbarButtons.includes('fontSize') && (
          <CustomSelect
            value={editor.getAttributes('fontSize').fontSize || ''}
            options={(fontSizeOptions || fontSizes).map(f => ({ value: f.value, label: f.name }))}
            onChange={val => editor.chain().focus().setFontSize(val).run()}
            className="richer-editor-select"
            placeholder={labels.fontSize || 'Font Size'}
            aria-label={labels.fontSize || 'Font Size'}
          />
        )}
        {/* Font Family Dropdown */}
        {!excludeToolbarButtons.includes('fontFamily') && (
          <CustomSelect
            value={editor.getAttributes('fontFamily').fontFamily || ''}
            options={(fontFamilyOptions || fontFamilies).map(f => ({ value: f.value, label: <span style={{ fontFamily: f.value }}>{f.name}</span> }))}
            onChange={val => editor.chain().focus().setFontFamily(val).run()}
            className="richer-editor-select"
            placeholder="Font Family"
            aria-label="Font Family"
          />
        )}
        <div className="toolbar-divider" />
        {/* Bold */}
        {!excludeToolbarButtons.includes('bold') && (
          <button onClick={() => editor.chain().focus().toggleBold().run()} className={`richer-editor-button${editor.isActive("bold") ? ' richer-editor-buttonActive' : ''}`} type="button" aria-label={labels.bold} title={labels.bold}><BoldIcon size={16} /></button>
        )}
        {/* Underline */}
        {!excludeToolbarButtons.includes('underline') && (
          <button onClick={() => editor.chain().focus().toggleUnderline().run()} className={`richer-editor-button${editor.isActive("underline") ? ' richer-editor-buttonActive' : ''}`} type="button" aria-label={labels.underline} title={labels.underline}><UnderlineIcon size={16} /></button>
        )}
        {/* Highlighter (single color) */}
        {!excludeToolbarButtons.includes('highlight') && (
          <button onClick={() => editor.chain().focus().toggleHighlight({ color: '#fff59d' }).run()} className={`richer-editor-button${editor.isActive("highlight") ? ' richer-editor-buttonActive' : ''}`} type="button" aria-label={labels.highlight} title={labels.highlight}><Highlighter size={16} /></button>
        )}
        {/* Code block */}
        {!excludeToolbarButtons.includes('code') && (
          <button onClick={() => editor.chain().focus().toggleCodeBlock().run()} className={`richer-editor-button${editor.isActive("codeBlock") ? ' richer-editor-buttonActive' : ''}`} type="button" aria-label={labels.code} title={labels.code}><CodeIcon size={16} /></button>
        )}
        <div className="toolbar-divider" />
        {/* Unordered List */}
        {!excludeToolbarButtons.includes('bulletList') && (
          <button onClick={() => editor.chain().focus().toggleBulletList().run()} className={`richer-editor-button${editor.isActive("bulletList") ? ' richer-editor-buttonActive' : ''}`} type="button" aria-label={labels.bulletList} title={labels.bulletList}><BulletListIcon size={16} /></button>
        )}
        {/* Ordered List */}
        {!excludeToolbarButtons.includes('orderedList') && (
          <button onClick={() => editor.chain().focus().toggleOrderedList().run()} className={`richer-editor-button${editor.isActive("orderedList") ? ' richer-editor-buttonActive' : ''}`} type="button" aria-label={labels.orderedList} title={labels.orderedList}><OrderedListIcon size={16} /></button>
        )}
        <div className="toolbar-divider" />
        {/* Link Popover */}
        {!excludeToolbarButtons.includes('link') && (
          <>
            <button
              ref={linkButtonRef}
              onClick={() => {
                setLinkPopoverOpen((open) => !open);
                setLinkUrl(editor.getAttributes('link').href || '');
                setLinkTarget(editor.getAttributes('link').target || '_blank');
              }}
              className={`richer-editor-button${editor.isActive("link") ? ' richer-editor-buttonActive' : ''}`}
              type="button"
              aria-label={labels.link}
              title={labels.link}
            >
              <LinkIcon size={16} />
            </button>
            <CustomPopover
              open={linkPopoverOpen}
              onOpenChange={setLinkPopoverOpen}
              anchorEl={linkButtonRef.current}
              closeButton
              onEsc={() => setLinkPopoverOpen(false)}
            >
              <div className="mb-2 font-semibold text-base">{labels.link}</div>
              <input
                type="text"
                placeholder="Paste link URL here..."
                value={linkUrl}
                onChange={e => setLinkUrl(e.target.value)}
                className="richer-editor-input"
                autoFocus
              />
              <select
                name="target"
                value={linkTarget}
                onChange={e => setLinkTarget(e.target.value)}
                className="richer-editor-input"
                style={{ marginBottom: 8 }}
              >
                <option value="_blank">New Tab (_blank)</option>
                <option value="_self">Same Tab (_self)</option>
                <option value="_parent">Parent Frame (_parent)</option>
                <option value="_top">Top Frame (_top)</option>
              </select>
              <div className="richer-editor-flexRow">
                <button
                  className="richer-editor-primaryBtn"
                  onClick={handleLinkInsert}
                  disabled={!linkUrl}
                >
                  {labels.insert}
                </button>
                <button
                  className="richer-editor-secondaryBtn"
                  onClick={handleLinkUnset}
                  disabled={!editor.isActive('link')}
                >
                  {labels.remove}
                </button>
              </div>
            </CustomPopover>
          </>
        )}
        {/* Image Popover */}
        {!excludeToolbarButtons.includes('image') && (
          <>
            <button
              ref={imageButtonRef}
              onClick={() => {
                setImagePopoverOpen((open) => !open);
                if (!imageUploadUrl) setImageTab('url');
              }}
              className="richer-editor-button"
              type="button"
              aria-label={labels.image}
              title={labels.image}
            >
              <ImageIcon size={16} />
            </button>
            <CustomPopover
              open={imagePopoverOpen}
              onOpenChange={setImagePopoverOpen}
              anchorEl={imageButtonRef.current}
              closeButton
              onEsc={() => setImagePopoverOpen(false)}
            >
              <div className="mb-2 font-semibold text-base flex gap-4 border-b pb-2">
                <button className={`richer-editor-button${imageTab === 'url' ? ' richer-editor-buttonActive' : ''}`} onClick={() => setImageTab('url')}>URL</button>
                {imageUploadUrl && (
                  <button className={`richer-editor-button${imageTab === 'upload' ? ' richer-editor-buttonActive' : ''}`} onClick={() => setImageTab('upload')}>Upload</button>
                )}
              </div>
              {imageTab === 'url' && (
                <>
                  <input
                    type="text"
                    placeholder="Paste image URL here..."
                    value={imageUrl}
                    onChange={e => setImageUrl(e.target.value)}
                    className="richer-editor-input"
                    autoFocus
                  />
                  <div className="richer-editor-flexRowMb2">
                    <input
                      type="text"
                      placeholder="Width (e.g. 400 or 50%)"
                      value={imageWidth}
                      onChange={e => setImageWidth(e.target.value)}
                      className="richer-editor-input"
                    />
                    <input
                      type="text"
                      placeholder="Height (e.g. 300 or 50%)"
                      value={imageHeight}
                      onChange={e => setImageHeight(e.target.value)}
                      className="richer-editor-input"
                    />
                  </div>
                  <div className="richer-editor-textXs">Leave blank for default size. Use px (e.g. 400) or % (e.g. 50%).</div>
                  <button
                    className="richer-editor-primaryBtn"
                    onClick={handleImageUrlInsert}
                    disabled={!imageUrl}
                  >
                    {labels.image}
                  </button>
                </>
              )}
              {imageTab === 'upload' && imageUploadUrl && (
                <>
                  {!uploadedImageUrl && (
                    <>
                      <input
                        type="file"
                        accept="image/*"
                        onChange={handleUploadInputChange}
                        className="mb2"
                        disabled={uploading}
                      />
                      {uploading && <div className="text-sm text-blue-600 mb-2">Uploading...</div>}
                      {uploadError && <div className="text-sm text-red-600 mb-2">{uploadError}</div>}
                    </>
                  )}
                  {uploadedImageUrl && (
                    <>
                      <div className="mb-2 flex flex-col items-center">
                        <img src={uploadedImageUrl} alt="Preview" className="max-h-40 max-w-full rounded border mb-2" />
                      </div>
                      <div className="richer-editor-flexRowMb2">
                        <input
                          type="text"
                          placeholder="Width (e.g. 400 or 50%)"
                          value={imageWidth}
                          onChange={e => setImageWidth(e.target.value)}
                          className="richer-editor-input"
                        />
                        <input
                          type="text"
                          placeholder="Height (e.g. 300 or 50%)"
                          value={imageHeight}
                          onChange={e => setImageHeight(e.target.value)}
                          className="richer-editor-input"
                        />
                      </div>
                      <div className="richer-editor-flexRow">
                        <button
                          className="richer-editor-primaryBtn"
                          onClick={handleUploadedImageInsert}
                        >
                          {labels.add}
                        </button>
                        <button
                          className="richer-editor-secondaryBtn"
                          onClick={() => {
                            setUploadedImageUrl('');
                            setImageFile(null);
                            setImageWidth('');
                            setImageHeight('');
                            setUploadError(null);
                          }}
                        >
                          {labels.cancel}
                        </button>
                      </div>
                    </>
                  )}
                </>
              )}
            </CustomPopover>
          </>
        )}
        {/* Video Popover */}
        {!excludeToolbarButtons.includes('video') && (
          <>
            <button
              ref={videoButtonRef}
              onClick={() => setVideoPopoverOpen((open) => !open)}
              className="richer-editor-button"
              type="button"
              aria-label={labels.video}
              title={labels.video}
            >
              <VideoIcon size={16} />
            </button>
            <CustomPopover
              open={videoPopoverOpen}
              onOpenChange={setVideoPopoverOpen}
              anchorEl={videoButtonRef.current}
              closeButton
              onEsc={() => setVideoPopoverOpen(false)}
            >
              <div className="mb-2 font-semibold text-base">{labels.video}</div>
              <input
                type="text"
                placeholder="Paste YouTube video URL here..."
                value={videoUrl}
                onChange={e => setVideoUrl(e.target.value)}
                className="richer-editor-input"
                autoFocus
              />
              <div className="richer-editor-flexRowMb2">
                <input
                  type="text"
                  placeholder="Width (e.g. 400 or 50%)"
                  value={videoWidth}
                  onChange={e => setVideoWidth(e.target.value)}
                  className="richer-editor-input"
                />
                <input
                  type="text"
                  placeholder="Height (e.g. 300 or 50%)"
                  value={videoHeight}
                  onChange={e => setVideoHeight(e.target.value)}
                  className="richer-editor-input"
                />
              </div>
              <div className="richer-editor-textXs">Leave blank for default size. Use px (e.g. 400) or % (e.g. 50%).</div>
              <button
                className="richer-editor-primaryBtn"
                onClick={handleVideoUrlInsert}
                disabled={!videoUrl}
              >
                {labels.video}
              </button>
            </CustomPopover>
          </>
        )}
        {/* Alignment Dropdown */}
        {!excludeToolbarButtons.includes('align') && (
          <CustomSelect
            value={editor.getAttributes('textAlign') || 'left'}
            options={alignmentOptions.map(opt => ({ value: opt.value, label: <>{opt.label} {opt.name}</> }))}
            onChange={val => editor.chain().focus().setTextAlign(val).run()}
            className="richer-editor-select"
            placeholder={labels.align}
          />
        )}
      </div>
    </>
  );
};

// Update SmallRicherEditorProps to match RicherEditor
interface SmallRicherEditorProps {
  content?: string | object;
  onChange?: (value: string | object) => void;
  imageUploadUrl?: string;
  placeholder?: string;
  minHeight?: string;
  maxHeight?: string;
  editorProps?: any;
  outputFormat?: 'html' | 'json';
  readOnly?: boolean;
  className?: string;
  style?: React.CSSProperties;
  excludeToolbarButtons?: string[];
  i18n?: Record<string, string>;
  fontSizeOptions?: { name: string; value: string }[];
  fontFamilyOptions?: { name: string; value: string }[];
}

const getSafeContent = (content: any, outputFormat: 'html' | 'json') => {
  if (outputFormat === 'json') {
    if (typeof content === 'object' && content !== null) {
      return content;
    }
    if (typeof content === 'string') {
      try {
        const parsed = JSON.parse(content);
        if (typeof parsed === 'object' && parsed !== null) {
          return parsed;
        }
      } catch {
        // ignore
      }
    }
    // fallback to empty doc
    return { type: 'doc', content: [{ type: 'paragraph' }] };
  } else {
    // outputFormat === 'html'
    if (typeof content === 'string') {
      return content;
    }
    if (typeof content === 'object' && content !== null) {
      // Try to convert JSON to HTML using Tiptap's generateHTML if available
      // But since we don't have access here, fallback to empty string
      return '';
    }
    return '';
  }
};

const SmallRicherEditor  = ({
  content = '',
  onChange,
  imageUploadUrl,
  placeholder,
  minHeight,
  maxHeight,
  editorProps = {},
  outputFormat = 'html',
  readOnly = false,
  className = '',
  style = {},
  excludeToolbarButtons = [],
  i18n = {},
  fontSizeOptions,
  fontFamilyOptions,
}: SmallRicherEditorProps) => {
  // Use safe content conversion
  const safeContent = React.useMemo(() => getSafeContent(content, outputFormat), [content, outputFormat]);
  const editor = useEditor({
    extensions: [
      StarterKit,
      Link.configure({ openOnClick: true }),
      Highlight.configure({ multicolor: false, HTMLAttributes: { style: 'background-color: #fff59d' } }),
      TextAlign.configure({ types: ["paragraph"] }),
      Image,
      Youtube.configure({
        controls: true,
        allowFullscreen: true,
        width: 640,
        height: 360,
        HTMLAttributes: {
          class: 'mx-auto my-4 rounded-lg shadow',
        },
      }),
      TextStyle,
      FontSize,
      Placeholder.configure({
        placeholder: placeholder || 'Write something...'
      }),
    ],
    content: safeContent,
    editorProps: {
      ...editorProps,
      attributes: {
        class: `richer-editor-textarea ${className || ''}`,
        style: `${minHeight ? `min-height:${minHeight};` : ''}${editorProps?.attributes?.style || ''}`,
        spellCheck: 'true',
        readOnly: readOnly ? 'true' : undefined,
        ...editorProps?.attributes,
      },
    },
    onUpdate({ editor }) {
      if (onChange) {
        if (outputFormat === 'json') {
          onChange(editor.getJSON());
        } else {
          onChange(editor.getHTML());
        }
      }
    },
    editable: !readOnly,
    immediatelyRender: false,
  });

  // In the useEffect, compare against safeContent instead of content
  React.useEffect(() => {
    if (editor && content !== undefined) {
      const current = outputFormat === 'json' ? editor.getJSON() : editor.getHTML();
      // Only update if different
      if (outputFormat === 'json') {
        let parsed: any = content;
        if (typeof content === 'string') {
          try {
            parsed = JSON.parse(content);
          } catch {
            parsed = { type: 'doc', content: [{ type: 'paragraph' }] };
          }
        }
        if (JSON.stringify(current) !== JSON.stringify(parsed)) {
          editor.commands.setContent(getSafeContent(content, outputFormat));
        }
      } else {
        // html
        if (current !== content) {
          editor.commands.setContent(getSafeContent(content, outputFormat));
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [content]);

  return (
      <div className={`richer-editor-roundedMdBorder`}>
        <MenuBar editor={editor} imageUploadUrl={imageUploadUrl} excludeToolbarButtons={excludeToolbarButtons} i18n={i18n} fontSizeOptions={fontSizeOptions} fontFamilyOptions={fontFamilyOptions} />
        <div className="richer-editor-overflowAuto" style={{maxHeight: maxHeight}}>
          <EditorContent editor={editor} />
        </div>
      </div>
  );
};

export default SmallRicherEditor;